.wp-block-accordion-panel-accordion-panel{
	flex-grow: 0;
	display: flex;
	--transTime:0.5s;
	--transEase:cubic-bezier(.72,.72,.01,.99);
	transition:flex-grow var(--transTime) var(--transEase);
	position: relative;
	align-self: stretch;
}
.is-acc-vertical .wp-block-accordion-panel-accordion-panel{
	flex-direction: column;
}
.wp-block-accordion-panel-accordion-panel.is-panel-open{
	flex-grow: 1;
}

// Panel Header

.wp-block-accordion-panel-accordion-panel .panel-header{
	text-decoration: none;
	position: relative;
}
.is-acc-horizontal .wp-block-accordion-panel-accordion-panel .panel-header{
	writing-mode: vertical-lr;
	text-orientation:sideways-right;
}

// Panel headings

.wp-block-accordion-panel-accordion-panel .panel-header .panel-headings{
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 1fr;
	overflow: hidden;
}

// Panel Heading

.wp-block-accordion-panel-accordion-panel .panel-header .panel-heading{
	grid-area: 1/1/1/1;
	position: relative;
	transition: transform calc(var(--transTime)/1) cubic-bezier(.33,0,.02,.99);
	padding:var(--panelHeaderPad) 0;
	text-align: var(--headerAlignment);
	--closeTrnsTo: translate(-100%, 0%);
	--openTrnsFrom: translate(101%, 0%);
}
.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel-header .panel-heading{
	--closeTrnsTo: translate(0%, -100%);
	--openTrnsFrom: translate(0%, 101%);
	padding:0 var(--panelHeaderPad);
}

.wp-block-accordion-panel-accordion-panel .panel-header .panel-heading *{
	color:inherit;
}

.wp-block-accordion-panel-accordion-panel .panel-header .panel-heading-close{
	color: var(--panelHeadingColorClose);
	transform: translate(0%, 0%);
}
.wp-block-accordion-panel-accordion-panel:is(
	:hover, .is-panel-open
) .panel-header .panel-heading-close{
	transform: var(--closeTrnsTo);
}

.wp-block-accordion-panel-accordion-panel .panel-header .panel-heading-open{
	color: var(--panelHeadingColorOpen);
	background-color: var(--panelBgColor);
	transform: var(--openTrnsFrom);
}
.wp-block-accordion-panel-accordion-panel:is(
	:hover, .is-panel-open
) .panel-header .panel-heading-open{
	transform: translate(0, 0%);
}


// Panel

.wp-block-accordion-panel-accordion-panel .panel{
	width: 0px;
	overflow-x: hidden;
	overflow-y: scroll;
	max-height: var(--maxPanelHeight);
	z-index: 2;
	transition: var(--transTime) var(--transEase);
	background-color: var(--panelBgColor);
	flex-grow: 0;
}
.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel{
	width: 100%;
	height: 0px;
	overflow-y: scroll;
}

.wp-block-accordion-panel-accordion-panel.is-panel-open .panel{
	flex-grow: 1;
}



// Panel Content

.wp-block-accordion-panel-accordion-panel .panel-content{
	align-self: stretch;
	width: var(--availContentW);

}
.wp-block-accordion-panel-accordion-panel.is-panel-open .panel-content{
	display: block;
	flex-grow: 1;
}

// Accordion Rule

.wp-block-accordion-panel-accordion-panel .panel-header::before,
.wp-block-accordion-panel-accordion-panel .panel-headings::after{
	content:'';
	display: block;
	position:absolute;
	background-color: var(--ruleColor);
	--ruleWidth:4px;
	left: 0px;
	z-index: 10;
	width: var(--ruleWidth);
	height: 100%;
}

.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel-header::before,
.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel-headings::after{
	width: 100%;
	height: var(--ruleWidth);
}


.wp-block-accordion-panel-accordion-panel .panel-header::before{
	left:calc(-1 * var(--ruleWidth));
}
.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel-header::before{
	top:calc(-1 * var(--ruleWidth));
	left: 0px;
}

.wp-block-accordion-panel-accordion-panel .panel-headings::after{
	right:0px;
	left: auto;
}
.is-acc-vertical .wp-block-accordion-panel-accordion-panel .panel-headings::after{
	bottom:0;
	right: auto;
}








